body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Death Star";
  font-style:normal;
  font-weight:normal;
  src: 
      url("/star-wars-api-box/static/media/Death Star.ade91cb0.otf") format("truetype")
}

body {
  background: url(/star-wars-api-box/static/media/SWAPI-background.5d6a625d.jpg);
}

.App {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.header {
  align-items: center;
  background: url(/star-wars-api-box/static/media/SWAPI-background.5d6a625d.jpg);
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  height: 20%;
  justify-content: center;
  position: relative;
  z-index: 1;
}

.app-title {
  color: #f9d500;
  font-family: "Death Star";
  font-size: 10rem;
  margin: 3rem 0 0 0;
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-color: #f9d500;
  -webkit-text-stroke-width: 3px;
}

.title-i {
  -webkit-text-stroke-width: 7px;
}

.error-popup-placeholder {
  height: 2rem; 
  margin: 0 0 3rem; 
}

@media (max-width: 1060px) {
