.Sidebar-hidden {
  display: none;
}

.fade {
  background-image: linear-gradient(0deg, transparent, black 75%);
  min-height: 20vh;
  position: relative;
  top: -25px;
  width: 100%;
  z-index: 1;
}

.star-wars {
  background: url(/star-wars-api-box/static/media/SWAPI-background.5d6a625d.jpg);
  color: #feda4a;
  display: flex;
  font-family: 'Pathway Gothic One', sans-serif;
  font-size: 550%;
  font-weight: 600;
  height: 30rem;
  justify-content: center;
  letter-spacing: 4px;
  line-height: 125%;
  overflow: hidden;
  -webkit-perspective: 400px;
          perspective: 400px;
  position: relative;
  text-align: justify;
  top: -1rem;
  word-spacing: normal;
  z-index: 1;
}

.crawl {
  -webkit-animation: crawl 90s linear;
          animation: crawl 90s linear;
  position: relative;
  -webkit-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
}

.title {
  text-align: center;
}

.film-title {
  font-size: 150%;
  font-weight: 600;
}

@-webkit-keyframes crawl {
  0% {
    top: 0px;
    -webkit-transform: rotateX(20deg)  translateZ(0);
            transform: rotateX(20deg)  translateZ(0);
  }
  100% { 
    top: -6000px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px);
  }
}

@keyframes crawl {
  0% {
    top: 0px;
    -webkit-transform: rotateX(20deg)  translateZ(0);
            transform: rotateX(20deg)  translateZ(0);
  }
  100% { 
    top: -6000px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px);
  }
}

.CardContainer {
  background: url(/star-wars-api-box/static/media/SWAPI-background.5d6a625d.jpg);
	border: 1px solid black;
  border-radius: 10px;
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat( auto-fit, minmax(17rem, 1fr) );
  margin: 0 5rem 3rem;
}

@media (max-width: 720px) {
  .CardContainer {
   margin: 0 auto 3rem
  }
}
.Card {
  background: url(/star-wars-api-box/static/media/swapi_rough_gray.a5bc7f4f.jpg);
  border-radius: 50px;
  color: white;
  cursor: pointer;
  font-family: 'Pathway Gothic One', sans-serif;
  font-size: 1.1rem;
  height: 20rem;
  letter-spacing: 0.1rem;
}

.Card:hover {
  box-shadow: #d9ffd9 0px 0px 15px 2px;
}

.Card-active {
	box-shadow: #d9ffd9 0px 0px 15px 2px;
}

.fav-btn-card-container {
	align-items: center;
	display: flex;
	justify-content: space-between;
	padding: 0.5rem 2rem;
}

.fav-btn {
	align-items: center;
	border-radius: 50%;
	box-shadow: ;
	cursor: pointer;
	display: flex;
	height: 3.5rem;
	justify-content: center;
	width: 3.5rem;
}

.fav-btn:hover {
 	border-radius: 50%;
  box-shadow: #d9ffd9 0px 0px 20px 2px;
}

.fav-btn:focus {
  outline: 0;
}


.fav-btn-inactive {
	background-color: #FFF;
	border-radius: 50%;
}

.fav-btn-active {
	background-color: #ffdf00;
